.home {
  height: 100dvh;
  position: relative;
  padding-top: 5%;
}
.home .screen-root {
  height: 90%;
  width: 70%;
  border: 1px solid var(--color-white);
  margin: auto;
  position: relative;
  display: grid;
  grid-template-rows: 1fr 50px;
}
.home .screen-root > div {
  border: 1px solid var(--color-white);
}
