.bottom-bar {
  display: flex;
  justify-content: space-between;
}
.bottom-bar > div {
  display: flex;
  align-items: center;
}
.bottom-bar svg {
  height: 25px;
  width: 25px;
}
.bottom-bar .svg-wrap.bigger svg {
  height: 40px;
  width: 40px;
}
.bottom-bar .item {
  padding: 0 10px;
}
.bottom-bar .svg-wrap > div {
  height: fit-content;
  display: flex;
}
.bottom-apps {
  border-left: 1px solid var(--color-white);
  height: 100%;
  display: flex;
  align-items: center;
}
.time-date {
  display: flex;
  flex-direction: column;
  align-items: center;
}
