.name-drop {
  position: relative;
  overflow: hidden;
}
.letters-wrap {
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.letter {
  position: relative;
  display: inline-block;
}
.letter h1 {
  font-size: 94px;
}
.letter.centered {
  transform: translate(0, 0);
}
.piston {
  position: absolute;
  background-color: var(--color-white);
}
.piston.left {
  top: 0;
  left: 100%;
}
.piston.right {
  top: 0;
  right: 100%;
}
.piston.up {
  top: 100%;
  right: 0;
}
.piston.down {
  bottom: 100%;
  left: 0;
}
.piston-bar {
  position: absolute;
  background-color: var(--color-white);
}
.piston.right .piston-bar,
.piston.left .piston-bar {
  width: 200px;
  height: 6px;
  top: 50%;
  transform: translateY(-50%);
}
.piston.right .piston-bar {
  right: 100%;
}
.piston.left .piston-bar {
  left: 100%;
}
.piston.up .piston-bar,
.piston.down .piston-bar {
  height: 200px;
  width: 10px;
  left: 50%;
  transform: translateX(-50%);
}
.piston.up .piston-bar {
  top: 100%;
}
.piston.down .piston-bar {
  bottom: 100%;
}
