:root {
  --color-black: #060606;
  --color-white: #f8f8f8;
  --color-main: #61dafb;
  --color-sec: #3eda3c;
  --color-main-dark: #1d7b95;
  --header-height: 129px;
}
.app {
  background-color: var(--color-black);
  color: var(--color-white);
  font-family: "Raleway", sans-serif;
  max-width: 100dvw;
}
a {
  text-decoration: none;
  color: inherit;
}
* {
  box-sizing: border-box;
  margin: 0;
  font-weight: 400;
  user-select: none;
  /* For older browser compatibility */
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
img {
  height: 100%;
  width: 100%;
}
.page {
  padding: 60px 0 200px;
}
.wrapper {
  width: calc(100% - 120px);
  max-width: 1440px;
  margin: auto;
}
.row {
  display: flex;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1em;
}
h1.bigger {
  font-size: 96px;
}
h1 {
  font-size: 76px;
}
h2 {
  font-size: 54px;
}
h3 {
  font-size: 40px;
}
h4 {
  font-size: 32px;
}
h5 {
  font-size: 24px;
}
h6 {
  font-size: 18px;
}
.app .loader {
  background-color: var(--color-black);
  position: fixed;
  top: 0;
  height: 100dvh;
  width: 100dvw;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 1;
  pointer-events: all;
  transition: opacity 500ms linear;
  flex-direction: column;
  gap: 40px;
}
.app .loader.hidden {
  opacity: 0;
  pointer-events: none;
}
.app svg {
  fill: var(--color-white);
  height: 100%;
  width: auto;
}
.app .loader .under-construction {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  opacity: 1;
  transition: opacity 500ms ease-out;
}
.app .loader .under-construction p {
  text-decoration: underline;
  cursor: pointer;
}
.app .loader .under-construction.hidden {
  opacity: 0;
}
