.screen {
  padding: 20px;
}
.screen-grid {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
  grid-template-rows: repeat(8, 1fr);
  height: 100%;
  width: 100%;
}
.screen-grid > div {
  position: relative;
}
.screen-grid .shortcut {
  position: absolute;
  height: 100%;
  width: 100%;
  padding: 5px;
}
.screen-grid .shortcut .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  padding: 5px;
}
.screen-grid .shortcut:hover .content {
  background-color: rgba(255, 255, 255, 0.05);
}
.screen-grid .shortcut .icon {
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: center;
}
.screen-grid .shortcut p {
  max-width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-size: 14px;
  height: 1.5em;
}
.stats {
  position: absolute;
  left: 100%;
  width: 200px;
}
